.loadbar {
  height: 8px;
  background-color: #5a6071;
  border-radius: .25rem;
  width: 33.33333%;
  position: relative;
}
.loadbar .bar {
  height: 6px;
  top: 1px;
  left: 1px;
  right: 1px;
  bottom: 1px;
  position: absolute;
  background-color: #2b6ed8;
  background: linear-gradient(45deg, #2b6ed8, #61ddde);
  -webkit-transition: 0.3s width ease;
  transition: 0.3s width ease;
}
.alert-message {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 20;
  border-radius: .25rem;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background-color: #5a6071;
  color: #ffffff;
  -webkit-box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
          box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.11), 0 5px 15px 0 rgba(0, 0, 0, 0.08);
  padding: 1rem;
  margin-right: 2rem;
  width: 40%;
}
.alert-message h5 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 1rem;
}
.alert-message h6 {
  margin-bottom: 0.25rem;
}
.alert-message ul {
  list-style: none;
  padding: 0;
}
.alert-message ul li {
  border-bottom-width: 1px;
  border-color: #5a6071;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
}
.alert-message ul li:last-child {
  margin-bottom: 0;
}
.alert-message.error {
  background-color: #cc1f1a;
}
.alert-message.error li {
  border-color: #f66d9b;
}
.alert-message.error h5,
.alert-message.error h6,
.alert-message.error p,
.alert-message.error li {
  color: #ffffff;
}
.absolute-center {
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-in-out-enter-active {
  -webkit-transition: all .3s ease .6s;
  transition: all .3s ease .6s;
}
.slide-in-out-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-in-out-enter,
.slide-in-out-leave-to {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active,
.list-leave-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}
